import React from "react";
import PropTypes from 'prop-types';
import { makeStyles } from '@mui/styles';
import styles from "./styles";
import {useScrollTrigger,Slide,AppBar,Button,useMediaQuery,Drawer} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import SegmentIcon from '@mui/icons-material/Segment';
import CloseIcon from '@mui/icons-material/Close';
import { fadeInDown } from 'react-animations';
import Radium, {StyleRoot} from 'radium';

import { ReactComponent as Logo } from "../../assets/images/shameem_logo.svg"
import  cv from "../../assets/documents/muhammed_shameem_cv.pdf";

const useStyles = makeStyles(styles);

const style = {
  bounce: {
    animation: 'x 1s',
    animationName: Radium.keyframes(fadeInDown, 'fadeInDown')
  }
}

function HideOnScroll(props) {
    const { children, window } = props;
    const trigger = useScrollTrigger({
      target: window ? window() : undefined,
    });
  
    return (
      <Slide appear={false} direction="down" in={!trigger}>
        {children}
      </Slide>
    );
  }
  
  HideOnScroll.propTypes = {
    children: PropTypes.element.isRequired,
    window: PropTypes.func,
  };


export default function AppHeader(props){
    const classes = useStyles();
    const [sliderOpen,setSliderOpen]=React.useState(false)
    
    const theme = useTheme(); 
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

    const handleSliderOpen = () => {
      setSliderOpen(true)
    }

    const handleSliderClose = () => {
      setSliderOpen(false)
    }


    const Menu = <div className={classes.lastContainer}>
                    <a 
                      href="#about"
                      className={classes.links}
                      onClick={handleSliderClose}
                    >
                      <span className={classes.no}>01.</span>About
                    </a>
                    <a 
                      href="#experience"
                      className={classes.links}
                      onClick={handleSliderClose}
                    >
                      <span className={classes.no}>02.</span>Experience
                    </a>
                    {/* <a 
                      href="#work"
                      className={classes.links}
                      onClick={handleSliderClose}
                    >
                      <span className={classes.no}>03.</span>Work
                    </a> */}
                    <a 
                      href="#contact"
                      className={classes.links}
                      onClick={handleSliderClose}
                    >
                      <span className={classes.no}>03.</span>Contact
                    </a>
                    <Button href={cv} target="_blank" rel="noreferrer" onClick={handleSliderClose}>
                      Resume
                    </Button>
                </div>

    return(
      <StyleRoot>
        <HideOnScroll {...props}> 
            <AppBar>  
              <div  className={classes.appBar} style={style.bounce}>
                <a href="/" aria-label="home">
                  <Logo className={classes.logo}/>
                </a>
               {!isMobile && 
                  Menu
               }
               {isMobile && !sliderOpen &&
                  <SegmentIcon  fontSize="large" className={classes.icon} onClick={handleSliderOpen}/>
               }
               { isMobile && sliderOpen &&
                 <Drawer 
                   anchor="right"
                   open={sliderOpen}
                  onClose={handleSliderClose}
                  className={classes.drawer}
                 >
                  <div className={classes.drawerInnerContainer}>
                    <CloseIcon fontSize="large" className={classes.closeIcon} onClick={handleSliderClose}/>
                    {Menu}
                  </div>
                 </Drawer>
               }
               </div>
              
            </AppBar>
       </HideOnScroll>
       </StyleRoot>
    )
}