import React from "react";
import { makeStyles } from '@mui/styles';
import { Divider,Tabs,Tab,useMediaQuery } from "@mui/material";
import { useTheme } from '@mui/material/styles';
import styles from "./styles";

const useStyles = makeStyles(styles);

const tabStyle = {
    default_tab:{
        color:"#8892b0",
        textTransform: "capitalize"
    },
    active_tab:{
        color: "rgb(100, 255, 218)",
        textTransform: "capitalize"
    }
};





export default function Experience(){
    const classes = useStyles();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const getStyle= (isActive) =>{
        return isActive ? tabStyle.active_tab : tabStyle.default_tab 
    }


    return(
        <div className={classes.container} id="experience">
            <div className={classes.innerContainer}>
                    <div className={classes.headingContainer}>
                        <span className={classes.count}>02.</span>
                        <span className={classes.aboutText}>Where Ive Worked</span>
                        <Divider className={classes.divider}/>
                    </div>
                    <div className={classes.tabContainer}>
                        <Tabs
                            orientation={isMobile?"horizontal":"vertical"}
                            value={value}
                            onChange={handleChange}
                            variant="scrollable"
                            scrollButtons={false}
                            aria-label="scrollable prevent tabs example"
                            classes = {{vertical:classes.vertical,root:classes.vertical}}
                        >
                            <Tab label="Simelabs" style={getStyle(value === 0)}/>
                        </Tabs>
                        {value === 0 &&
                            <div className={classes.tabPanel}>
                                <div className={classes.designation}>
                                    Software Engineer 
                                    <a
                                        href="https://www.simelabs.com/" 
                                        rel="noreferrer" 
                                        target="_blank" 
                                        className={classes.companyLink}
                                    >
                                    @ Simelabs
                                    </a>
                                </div>
                                <div className={classes.duration}>
                                    Nov 2020 - Present
                                </div>
                                <ul>
                                    <li className={classes.marginLeft}>
                                        Working on development, maintenance and changes in the project when required.
                                    </li>
                                    <li className={classes.marginLeft}>
                                        Write modern, performant, maintainable code for a diverse array of client and 
                                        internal projects.
                                    </li>
                                    <li className={classes.marginLeft}>
                                        Work with a variety of different languages, platforms, frameworks,
                                         and content management systems such as JavaScript, TypeScript,
                                          React, Django, Django REST API and Next js.
                                    </li>
                                    <li className={classes.marginLeft}>
                                        Communicate with multi-disciplinary teams of engineers, designers, 
                                        project managers, and clients on a daily basis
                                    </li>
                                    <li className={classes.marginLeft}>
                                        Improve existing modules for performance or maintainability without affecting 
                                        business logics.
                                    </li>
                                    
                                </ul>
                            </div>
                        }
                    </div>
                    
            </div>
        </div>
    )
}